           <v-menu offset-y>
             <template v-slot:activator="{ on }">
               <v-col cols="2">
                 <span style="margin-left: 10px;">{{slot2[0]}}</span>
                 <v-icon v-on="on" class="body-1 mx-1">mdi-arrow-down-drop-circle-outline</v-icon>
               </v-col>
             </template>
             <v-list>
               <v-list-item v-for="([title], idx) of slot2Menu"
                            :key="title"
                            @click="$store.commit('product/selectSlot', { slot: 2, idx })"
               >
