           <v-col cols="3" />
        </v-row>
<!--        <div class="productContainer">-->
          <cloud-mining-card v-for="(item, index) in products.slice(0, 10)" :idx="index" :key="index" :item="item" />
          <v-lazy v-for="(item, index) in products.slice(10)" :key="index + 10">
            <cloud-mining-card :item="item" />
          </v-lazy>
<!--        </div>-->
