<script>
import { mapState, mapGetters } from "vuex";

import { getDurationAlias } from "@/api/contract/cloud-mining";
import { price, unit, hashrate, totalReward } from "../filters";

import { coins } from "@/api/const";

import CoinSummary from "@/components/CoinSummary";
import CloudMiningCard from "@/components/product/CloudMiningCard";
import FilterPanel from "@/components/FilterPanel";

export default {
  name: "Products",
  filters: { price, unit, hashrate, totalReward },
  components: { CoinSummary, CloudMiningCard, FilterPanel },
  watch: {
    '$store.state.selectedCoins'() {
      this.filter.duration.splice(0);
      this.filter.issuers.splice(0);
    }
  },
  computed: {
    ...mapGetters("cloudMining", ["findContract"]),
    ...mapState("product", ["currency", "slot1", "slot2", "slot1Menu", "slot2Menu"]),
    ...mapState(["selectedCoins", "favorites", "summary", "blockchain"]),
    filters() {
      const products = this.findContract(this.selectedCoins[0]);

      const durationSet = new Set();
      const filters = products.reduce(
        (m, v) => {
          if (!durationSet.has(v.durationAlias)) {
            durationSet.add(v.durationAlias);
            m.durations.push([v.duration, v.durationAlias]);
          }

          if (m.issuers.indexOf(v.issuer) < 0) m.issuers.push(v.issuer);
          return m;
        },
        {
          durations: [],
          issuers: [],
          coins: this.coins,
          sort: [
            {
              text: " ROI",
              value: { field: item => item.total.roi, order: "desc", id: "roi" }
            },
            {
              text: " Unit price",
              value: { field: item => item.daily.preHalving.cost, order: "asc", id: "unit-price" }
            },
            {
              text: " Upfront fees",
              value: { field: item => item.total.upfront, order: "asc", id: "upfront" }
            }
          ]
        }
      );

      filters.durations = filters.durations.sort((a, b) => a[0] - b[0]).map(v => v[1]);
      return filters;
    },
    products() {
      const products = this.findContract(this.selectedCoins[0]);

      const res = products.filter(
        v =>
          (!this.filter.showFavOnly || this.favorites.indexOf(v.id) >= 0) &&
          // (!this.filter.showInStockOnly || v.sold_percent < 100) &&
          (this.filter.issuers.length === 0 ||
            this.filter.issuers.indexOf(v.issuer) >= 0) &&
          (this.filter.duration.length === 0 ||
            this.filter.duration.indexOf(v.durationAlias) >= 0)
      );

      const maxRoi = Math.max(...res.map(v => v.total.roi));
      res.forEach(v => v.isBestDeal = v.total.roi === maxRoi);

      if (this.filter.sort) {
        const { field, order } = this.filter.sort;
        res.sort((a, b) => {
          const fa = a[field] || field(a);
          const fb = b[field] || field(b);
          return order === "asc" ? fa - fb : fb - fa;
        });
      }

      return res;
    }
  },
  data() {
    const { coin, duration } = this.$route.query;
    if (coin) this.$store.state.selectedCoins = [coin];

    return {
      coins,
      csel: 0,
      drawer: false,
      activeCoinTab: coins.indexOf(coin || this.$store.state.selectedCoins[0]),
      filter: {
        duration: duration ? [getDurationAlias(duration * 1)] : [],
        issuers: [],
        sort: { field: item => item.total.roi, order: "desc", id: "roi" },
        showFavOnly: false,
        showInStockOnly: false
      }
    };
  }
};
</script>

